import { reportErrorBi } from './reportErrorBi';
import type { IErrorHandler } from '@wix/fe-essentials/http-client';
import { createErrorResolution } from './createErrorResolution';
import { createBiLoggerWithDefaults } from '../util/createBiLoggerWithDefaults';
import {
  ApplicationError,
  ErrorCodesMap,
  ErrorCodesMapConditionalEnforcement,
  ErrorHandlerAPI,
  ErrorHandlerEssentials,
  HandleErrorOptions,
  OnlineManager,
  PlatformShowError,
  ResolvedError,
  ShowErrorProps,
} from '@wix/error-handler-core/types';
import { ErrorAccessor } from '../state/ErrorAccessor';
import { TranslationsState } from '../state/TranslationsState';
import { reportErrorResolutionBi } from './reportErrorResolutionBi';
import { v4 as uuid } from 'uuid';
import { ErrorResolution } from '../state/ErrorResolution';
import { reportGetResolvedErrorBi } from './reportGetResolvedErrorBi';
import { reportShowErrorBi } from './reportShowErrorBi';
import { CommonErrorsMapState } from '../state/CommonErrorsMapState';
import { fallbackMessage } from '../fallbackMessage';
import { reportErrorRetryBi } from './reportErrorRetryBi';
import { ErrorHandlerV2 } from './ErrorHandlerV2';
import { dedupeRequestId } from '../util/dedupeRequestId';

export interface ErrorHandlerParams extends ErrorHandlerEssentials {
  readonly showError?: PlatformShowError;
  readonly onlineManager?: OnlineManager;
  readonly webWindow?: typeof window;
}

export class ErrorHandler implements IErrorHandler, ErrorHandlerAPI {
  readonly environment;
  readonly biDefaultsOverrides;
  readonly biLogger;
  readonly errorMonitor;
  readonly experiments;
  readonly platformShowError;
  readonly translations;
  readonly commonErrorsMapState;
  readonly onlineManager: OnlineManager;
  readonly webWindow?: Partial<typeof window>;
  _skipWaitTranslations?: boolean;
  v2: ErrorHandlerV2;

  constructor(params: ErrorHandlerParams) {
    this.environment = params.environment;
    this.biDefaultsOverrides = params.biDefaultsOverrides;
    this.biLogger = createBiLoggerWithDefaults(params);
    this.errorMonitor = params.createErrorMonitor({
      dsn: 'https://831e1d96e7944c6aae0c9ed9d6babd35@sentry.wixpress.com/5896',
    });
    this.experiments = params.createExperiments?.({
      scopes: ['error-handler'],
    });
    this.platformShowError = params.showError;
    this.translations = new TranslationsState(params);
    this.onlineManager =
      params.onlineManager ??
      (typeof navigator !== 'undefined' ? navigator : { onLine: true });
    this.webWindow =
      params.webWindow ?? (typeof window !== 'undefined' ? window : undefined);
    this.commonErrorsMapState = new CommonErrorsMapState(this);
    this.v2 = new ErrorHandlerV2(this);
  }

  private _setErrorHandlerSessionId(errorAccessor: ErrorAccessor) {
    const { errorHandlerState } = errorAccessor;

    if (!errorHandlerState) {
      return;
    }

    errorHandlerState.sessionId = uuid();
  }

  private _setErrorHandlerResolvedError(
    errorAccessor: ErrorAccessor,
    errorResolution: ErrorResolution,
  ) {
    const { errorHandlerState } = errorAccessor;

    if (!errorHandlerState) {
      return;
    }

    errorHandlerState.resolvedError = errorResolution.errorToShow;
  }

  _setErrorHandlerErrorCodesMap(
    errorAccessor: ErrorAccessor,
    options: {
      errorCodesMap?: ErrorCodesMap;
    } = {},
  ) {
    const { errorCodesMap } = options;

    reportErrorResolutionBi(this, {
      errorAccessor,
      errorCodesMap,
    });

    const { errorHandlerState } = errorAccessor;

    if (!errorHandlerState) {
      return;
    }

    errorHandlerState.errorCodesMap = errorCodesMap;
  }

  private _isErrorHandlerDisabled() {
    const { experiments } = this;
    return (
      experiments != null &&
      !experiments.enabled('specs.os.EnableErrorHandlerInEditor')
    );
  }

  handleError = (error: unknown, options: HandleErrorOptions = {}) => {
    if (this._isErrorHandlerDisabled()) {
      return;
    }

    const { errorMonitor } = this;

    try {
      const errorAccessor = new ErrorAccessor({ error });

      this._setErrorHandlerSessionId(errorAccessor);

      reportErrorBi(this, {
        errorAccessor,
        requestOptions: options.requestOptions,
      });
    } catch (e) {
      console.error(e);
      errorMonitor.captureException(e as Error);
    }
  };

  _resolveError(
    errorAccessor: ErrorAccessor,
    options: {
      errorCodesMap?: ErrorCodesMap;
    } = {},
  ) {
    if (this._isErrorHandlerDisabled()) {
      return;
    }

    const errorCodesMap =
      options.errorCodesMap ?? errorAccessor.errorHandlerState?.errorCodesMap;

    const errorResolution = createErrorResolution(this, {
      errorAccessor,
      errorCodesMap,
    });

    this._setErrorHandlerResolvedError(errorAccessor, errorResolution);
    return errorResolution;
  }

  withErrorHandler = async <T>(
    fn: () => Promise<T>,
    options: {
      errorCodesMap: ErrorCodesMapConditionalEnforcement<T>;
    },
  ) => {
    try {
      return await fn();
    } catch (error) {
      const errorAccessor = new ErrorAccessor({ error });
      this._setErrorHandlerErrorCodesMap(errorAccessor, options);
      if (process.env.NODE_ENV === 'test' && !this._skipWaitTranslations) {
        await this.commonErrorsMapState.getInitPromise();
      }
      throw error;
    }
  };

  getResolvedError = <
    TApplicationError extends ApplicationError = ApplicationError,
  >(
    error: unknown,
  ): ResolvedError<TApplicationError> => {
    const { errorMonitor } = this;

    try {
      const errorAccessor = new ErrorAccessor({ error });

      const {
        applicationError,
        validationError,
        httpError,
        errorHandlerState,
      } = errorAccessor;

      const errorResolution = this._resolveError(errorAccessor);

      const resolvedError = errorHandlerState?.resolvedError;

      const message = resolvedError?.message ?? fallbackMessage;

      reportGetResolvedErrorBi(this, {
        message,
        action: resolvedError?.action,
        errorAccessor,
        errorResolution,
      });

      return {
        message,
        action: resolvedError?.action,
        validationError,
        applicationError: applicationError as
          | TApplicationError
          | undefined
          | null,
        httpError,
        requestId: dedupeRequestId(httpError?.requestId),
      };
    } catch (e) {
      console.error(e);
      errorMonitor.captureException(e as Error);
      throw e;
    }
  };

  reportRetryAttempt = (error: unknown) => {
    const { errorMonitor } = this;

    try {
      const errorAccessor = new ErrorAccessor({ error });

      reportErrorRetryBi(this, {
        errorAccessor,
      });
    } catch (e) {
      console.error(e);
      errorMonitor.captureException(e as Error);
    }
  };

  showError = (error: unknown, props?: Partial<ShowErrorProps> | null) => {
    const { platformShowError, errorMonitor } = this;

    if (!platformShowError) {
      return;
    }

    try {
      const errorAccessor = new ErrorAccessor({ error });
      const { errorHandlerState } = errorAccessor;

      this._resolveError(errorAccessor);

      const resolvedError = errorHandlerState?.resolvedError;

      const showErrorProps = {
        ...resolvedError,
        ...props,
      };

      const message =
        props?.message ?? resolvedError?.message ?? fallbackMessage;

      const action = props?.action ?? resolvedError?.action;

      reportShowErrorBi(this, {
        message,
        action,
        errorAccessor,
      });

      platformShowError({
        ...showErrorProps,
        requestId: dedupeRequestId(errorAccessor.httpError?.requestId),
        message,
        action: action?.onClick
          ? {
              text: action.text,
              onClick: action.onClick,
            }
          : undefined,
      });
    } catch (e) {
      console.error(e);
      errorMonitor.captureException(e as Error);
    }
  };

  toJSON() {
    return {};
  }
}
